import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import styles from "./Introduce.module.scss";

gsap.registerPlugin(ScrollTrigger);

const Introduce = () => {
  const contentRef = useRef(null);
  const containerRef = useRef(null);

  useEffect(() => {
    const content = contentRef.current;
    const container = containerRef.current;
    const title = container.querySelector("h2");

    //     span  
    const splitText = (el) => {
      el.innerHTML = el.textContent
        .split(/(\S+)/)
        .map((word) =>
          word.trim()
            ? `<span class="${styles.word} ${
                word.startsWith("<span") ? styles.emphasis : ""
              }">${word}</span>`
            : word
        )
        .join("");
    };

    //  p   
    content.querySelectorAll("p").forEach((p) => splitText(p));

    // Introduce          
    gsap
      .timeline({
        scrollTrigger: {
          trigger: container,
          start: "center center", // Introduce     

          pin: true, // Introduce  
          scrub: true, //    
          onEnter: () => {
            ScrollTrigger.refresh(); //    
          },
        },
      })
      .fromTo(title, { opacity: 0, x: -50 }, { opacity: 1, x: 0, duration: 1 }) //  
      .fromTo(
        content.querySelectorAll(`.${styles.word}:not(.${styles.emphasis})`), //  
        { color: "#73706C", fontWeight: 300 }, //  :  ,   300
        {
          color: "#F2F2F2", //   :  
          fontWeight: 300, //   
          stagger: 0.05, //    ( )
          ease: "power1.inOut", //  
          duration: 1, //      
        }
      )
      .fromTo(
        content.querySelectorAll(`.${styles.emphasis}`), //  
        { color: "#22222220", fontWeight: 700 },
        {
          color: "#222",
          fontWeight: 700,
          stagger: 0.05,
          ease: "power1.inOut",
          duration: 1,
        },
        "<" //    
      );

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  return (
    <div ref={containerRef} className={styles.overviewContainer}>
      <h2 className={styles.title}>ABOUT ME,</h2>
      <div ref={contentRef} className={styles.contentContainer}>
        <p className={styles.content}>
            <span className={styles.emphasis}> </span>
            {" "}
          <span className={styles.emphasis}> </span> 
          .
          <br />  <span className={styles.emphasis}> </span>
                  
          , <br />
          <span className={styles.emphasis}> </span>{" "}
          <span className={styles.emphasis}> </span>   {" "}
          <span className={styles.emphasis}> </span> , <br />
             {" "}
          <span className={styles.emphasis}> </span>{" "}
          <span className={styles.emphasis}> </span>  
           . <br />
           <span className={styles.emphasis}></span>  
             <br />
          
          <span className={styles.emphasis}>  </span> 
          . <br />
           {" "}
          <span className={styles.emphasis}>  </span> 
          .
        </p>
      </div>
      <div className={styles.more}>
        <div className={styles.circle}></div>
        Learn more
      </div>
    </div>
  );
};

export default Introduce;
